section {
    display: flex;
    padding: 0.5125rem;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.625rem;
    min-width: 15em;
    flex: 1 0 0;
    align-self: stretch;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
    transition: all 1s linear;
    border-radius: 5px;
    cursor: pointer;
}

.image {
    display: flex;
    align-items: flex-start;
    align-self: stretch;
    position: relative;
}
.bgImage {
    width: 100%;
    height: auto;
}

.icon {
    position: absolute;
    right: 5%;
    top: 5%;
}

.icon:hover {
    cursor: pointer;
}

.promoOnImage {
    position: absolute;
    display: inline-flex;
    padding: 0.25rem 0.5rem;
    align-items: flex-start;
    gap: 0.625rem;
    border-radius: 0.25rem;
    background:#F63;
    bottom: 5%;
    left: 5%;
    color: #ffffff;
}
.descr {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
    gap: 0.3rem;
}

.descr .subtitle {
    color: black
}

.descrTitle {
    display: flex;
justify-content: space-between;
align-items: center;
align-self: stretch;
}

.descrTitle div:first-child .title{
    font-weight: bolder;
}

.promoPrice,
.price {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.3125rem;
}

.title {
    color: #606060;
}


.promoPrice .subtitle,
.price .subtitle {
    color: #BFBFBF;
}


.descrMiddle {
    display: flex;
flex-direction: column;
justify-content: center;
align-items: flex-start;
gap: 0.625rem;
flex: 1 0 0;
align-self: stretch;
}

  