.banner {
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-self: stretch;
    background-color: #E5FFDE;
    flex: 1 0 0;
    padding: 5px;
    border-radius: 5px;
    position: relative;
}

.banner:hover {
    background-color: #FCD5BA;
    padding: 5px;
    border-radius: 5px;
}

.text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
    width: 30%;
}
.title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
    font-weight: bold;
}
.subtitle {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex: 1 0 0;
}
.img {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-self: stretch;
    position: relative;
    padding: 5px;
}

.img img {

    height: 12em;
}