.filter {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.25rem;
    align-self: stretch;
}
.title:not(.container .title ) {
    display: flex;
    padding: 0.625rem 0.3125rem;
    align-items: center;
    align-self: stretch;
    color: #414141;
    font-weight: bold;
    background: #F3F2F1;
}
.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.875rem;
    flex: 1 0 0;
    align-self: stretch;
    position: relative;
}
.container .title {
    display: flex;
    align-items: flex-start;
    align-self: stretch;
}

.inputsPrice {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: stretch;
    gap: 10px;
}

.inputsPrice hr {
    border: 1px solid black;
    height: 10px;
    display: block;
    transform: rotate(90deg);
}

.inputsPrice input {
    display: flex;
    height: 2.5rem;
    padding: 0.5rem 1rem;
    align-items: flex-start;
    gap: 0.625rem;
    flex: 1 0 0;
    border-radius: 0.25rem;
    border: 1px solid #BFBFBF;
    background: #FFF;
    width: 100%;
}

.inputsRange {
    display: flex;
    justify-content: space-between;
}

.inputsRange input {
    width: 45%;
}
.containerStock {
    display: flex;
align-items: flex-start;
align-self: stretch;
}

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  } 
