div.correctBlock:first-child {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    padding-left: 5px;
}

.blocks {
    display: flex;
    flex-direction: column;
}

.order {
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    margin-bottom: 7.5rem;
}

.orderButton {
    margin: auto;
    display: flex;
    height: 3.75rem;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: #F3F2F1;
    margin-top: 3.75rem;
}

.header {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    flex: 1 0 0;
    justify-content: space-between;
}

.datetime {
    display: flex;
    gap: 10px;
}

.date,
.time {
    color: #414141;
    font-weight: bold;
}

.result {
    display: flex;
    padding: 0.25rem 0.5rem;
    align-items: flex-start;
    gap: 0.625rem;
    color: #414141;
    text-align: center;
}

.received {
    border-radius: 0.25rem;
    background: #70C05B;
    color: #F2F2F2;
}

.return {
    border-radius: 0.25rem;
    background: #D80000;
    color: #F2F2F2;
}

.allPrice {
    color: #414141;
}

.btns {
    display: flex;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: #70C05B;
    border: 1px solid #70C05B;
}

.body {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2.5rem;
    align-self: stretch;
}

.bodyCards {
    display: flex;
    align-items: flex-start;
    gap: 2.5rem;
    align-self: stretch;
    flex-wrap: wrap;
}

.bodyButton {
    display: flex;
    padding: 0.5rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    border-radius: 0.25rem;
    background: #F3F2F1;
    cursor: pointer;
}

.bodyButton button {
    color: #606060;
    text-align: center;
    display: flex;
    gap: 5px;
    padding: 5px 10px;
}

.blockCount {
    position: relative;
    display: flex;
    align-self: stretch;
}

.blockCount > div:last-child {
    position: absolute;
    top: 5%;
    left: 5%;
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: bold;
}

@media all and (max-width: 27em) {
    div.header:first-child {
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: center;
    }

    div.header:first-child .date,
    div.header:first-child .time,
    div.header:first-child .result,   
    div.header:first-child .btns   {
        font-size: 0.5em;
    }
}