.wrapper {
    display: flex;
    gap: 2.5rem;
}

.productsSection {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2.5rem;
    flex: 1 0 0;
    align-self: stretch;
}
.haveFilters {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 1rem;
}

.haveFilters button {
    display: flex;
    justify-content: center;
    gap: 0.5rem;
    align-items: center;
    padding: 0.5rem 1rem;
    border-radius: 0.3125rem;
}

.products {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    gap: 2.5rem;
    align-self: stretch;
    flex-wrap: wrap;
}
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    align-self: stretch;
    color: #606060;
}

.pagination svg {
    cursor: pointer;
}

@media screen and (max-width: 48em) {
    .wrapper {
        flex-direction: column;
    }
    .products {
        justify-content: center;
    }

    .products section {
        font-size: 2.4vw;
    }
}

