.section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    gap: 3.125rem;
    align-self: stretch;
    position: relative;
    border-radius: 0.5em;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
    padding: 5px;
}

.img {
    display: flex;
    align-items: flex-start;
}
.img img {
    width: 10em;
    height: auto;
}

.descr {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1 0 0;
    gap: 1.25rem;
}
.title {
    font-weight: bolder;
    font-size: 1.1em;
}
.container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 1.25rem;
}
.promo,
.normal {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
}
.promo .price {
    font-weight: bolder;
}

.btns {
    border-radius: 0.25rem;
    background: #70C05B;
    display: flex;
    gap: 1rem;
    padding: 5px;
    align-items: center;
}
.btns div {
    color: #FFF;
}

.containerPrice {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.25rem;
}

.containerPrice .promo {
    font-weight: bold;
}

.block {
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 0.25rem;
    border: 1px solid #BFBFBF;
    background: #FFF;
    left: 5px;
    top: 5px;
    accent-color: #70C05B;
}

.block input {
    z-index: 1;
    position: inherit;
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    opacity: 0;
    cursor: pointer;
}

.block div {
    position: inherit;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

input:checked + .slider {
    background: rgb(112, 192, 91);
}

@media screen and (max-width: 48em) {
    .section {
        flex-direction: column;
        gap: 1.2rem;
        flex: 1 0 0;
        justify-content: center;
    }

    .block {
        height: 2em;
        width: 2em;
    }

    .descr {
        align-self: stretch;
    }

    .descr .container {
        justify-content: flex-start;
        gap: 2rem;
    }

    .btns {
        align-self: stretch;
        justify-content: space-between;
        align-items: center;
    }

    .btns button {
        display: flex;
    }

    .containerPrice {
        flex-direction: row;
        align-self: flex-start;
    }
}