.panel {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
}

.catalog {
    cursor: pointer;
    width: 17em;
    height: 12.5em;
    display: flex;
    border-radius: 0.25rem;
    background-repeat: no-repeat;
    background-size: cover;
}

.catalog:last-child,
.catalog:first-child,
.catalog:nth-child(10) {
   max-width: 37em;
   width: 100%;
} 

.wrapper {
    flex: 1 0 0 ;
    align-self: flex-end;
    gap: 0.625rem;
    background: linear-gradient(180deg, rgba(255, 102, 51, 0.00) 0%, rgb(112, 192, 91) 82.81%);    
    box-shadow: 1px 2px 4px 0px rgba(112, 192, 91, 0.20);
    display: flex;
    padding: 5rem 0.625rem 0.625rem 0.625rem;
    flex-direction: column;
    align-items: flex-start;
    transition: all 1s linear;
    -webkit-transition: all 1s linear;  
    -moz-transition: all 1s linear;  
    -o-transition: all 1s linear;  
}

.catalog:hover .wrapper {
    background-position: 100px;
    background: linear-gradient(180deg, rgba(112, 192, 91, 0.00) 0%, rgb(255, 102, 51) 100%);    
    box-shadow: 4px 8px 16px 0px rgba(255, 102, 51, 0.20);
}  

.content {
    margin-top: auto;
    color: #fff;
    font-weight: bolder;
}


@media screen and (max-width: 27em) {

    .panel {
        justify-content: space-between;
    }
    .catalog {
        width: 12em;
        height: 10em;
        font-size: 3.5vw;
    }
    
    .catalog:last-child,
    .catalog:first-child,
    .catalog:nth-child(10) {
       max-width: 37em;
       width: 100%
    }
}