.mainContainer {
    display: flex;
    align-items: flex-start;
    gap: 9rem;
    align-self: stretch;
    flex: 1 0 0;
}

.left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.625rem;
}
.revTop {
    display: flex;
    align-items: center;
    gap: 0.625rem;
}

.revTop div:last-child {
    min-width: fit-content;
}

.revMiddle {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 0.625rem;
align-self: stretch;
}
.point {
    display: flex;
align-items: center;
gap: 0.625rem;
align-self: stretch;
justify-content: space-between;

}

.rate,
.exmStars {
    display: flex;
align-items: flex-start;
}
.count {
    display: flex;
justify-content: flex-end;
align-items: center;
}


.comments {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 2.5rem;
width: 50%;
}
.commentsUsers {
    display: flex;
flex-direction: column;
align-items: flex-start;
gap: 2.5rem;
}

.btn {
    cursor: pointer;
    background-color: lightgray;
    color: grey;
    padding: 1em 2em;
    border-radius: 0.5rem;
}

@media screen and (max-width: 27em) {
    .left {
        display: none;
    }

    .comments {
        width: 100%;
    }
}


