.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.25rem;
    flex: 1 0 0;
    align-self: stretch;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    gap: 10px;
    font-size: 2.5em;
    font-style: normal;
    font-weight: 700;
    color: #414141;
}

.header div:first-child {
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: 700;
    color: #414141;
    font-size: inherit;
}

.section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3.125rem;
    flex: 1 0 0;
    align-self: stretch;
    flex-wrap: wrap;
}

@media all and (max-width: 48em) {
    .header {
        padding: 5px 10px;
    }

    .section {
        flex-wrap: nowrap;
        justify-content: flex-start;
        padding: 15px;
        overflow-x: scroll;
    }

    .section::-webkit-scrollbar {
        display: none;
    }
}