.comment {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
}
.headerContainer {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}
.img {
    display: flex;
    align-items: flex-start;
    gap: 0.625rem;
    padding: 0.625rem;
}
.img img {
    width: 2em;
    height: 2em;
}
.text,
.name {
    color: #414141;
}
.bodyContainer{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.rate {
    display: flex;
    align-items: flex-start;
    gap: 0.25rem;
}
.date {
    color: #8F8F8F;
}