header {
    display: flex;
    padding: 0rem 1.25rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
}

header svg {
    color: #858585;
}

.wrapper {
    display: flex;
    gap: 0.625rem;
    align-self: stretch;

}

.bottomContainer,
.topContainer {
    display: flex;
    height: 4.25rem;
    padding: 0.625rem;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    flex: 1 0 0;
}

.bottomContainer {
    display: none;
}

.logoContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    cursor: pointer;
}

.catalogContainer {
    display: flex;
    align-items: flex-start;
    gap: 0.625rem;
    align-self: stretch;
}

.catalogContainer button {
    display: flex;
    padding: 0.6875rem 1.1875rem;
    justify-content: center;
    align-items: center;
    gap: 1.75rem;
    align-self: stretch;
    border-radius: 0.625rem;
    background: #70C05B;
    color: #FFF;
    text-align: center;
}

.inputContainer {
    display: flex;
    min-width: 6.25rem;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
    border-radius: 0.625rem;
    border: 1px solid #70C05B;
    position: relative;
}

.inputContainer input {
    display: flex;
    padding: 0.625rem;
    align-items: center;
    gap: 0.625rem;
    flex: 1 0 0;
    align-self: stretch;
    border-bottom: none;
    width: 100%;
}

.inputContainer img {
    position: relative;
    right: 1%;
}

.headerBottom,
.bottomContainer {
    display: none;
}



@media screen and (max-width: 27em) {
    header {
        display: none;
    }

    .headerBottom {
        display: flex;
        position: sticky;
        height: max-content;
        bottom: 0;
        background-color: #fff;
        z-index: 3;
        padding: 5px;
        box-shadow: -5px 10px 20px 0px;
    }
    
    .bottom {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .bottom img {
        width: 50%;
        height: auto;
    }

    .topContainer {
        display: none;
    }

    .bottomContainer {
        display: flex;
        justify-content: space-between;
        padding: 5px;
    }
    
    .bottom:last-child {
        flex-direction: row;
    }

    .bottom:last-child img{
        width: 10%;
    }
}

