/* SLIDERS */

.slider {
    background: url('../../../public/assets/sliders/pattern-sliders.svg');
    background-size: auto;
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 12.5rem;
    flex-shrink: 0;
}

.image {
    background-image: url('../../../public/assets/sliders/sliders.png');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 18.9375rem;
    height: 12.625rem;
    flex-shrink: 0;
}

.text {
    color: #414141;
    text-align: center;
    font-weight: bold;
    font-size: 2em;
}