article {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
    border-radius: 0.25rem;
    background: #FFF;
    box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.10);
    min-width: 15em;
    max-width: 20em;
}
.img {
    display: flex; 
    align-self: stretch;
}
.img  img {
    height: auto;
    width: 100%;
}

.descr {
    display: flex;
    padding: 0.625rem;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
    gap: 8px;
}

.date {
    color: #8F8F8F;
}

.title {
    color: #414141;
    font-weight: 700;
}

.subtitle {
    color: #414141;
}

.date,
.title,
.subtitle {
    display: flex;
    align-items: flex-start;
    align-self: stretch;
}