.detailed {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
    gap: 20px;
}

.data {
    display: flex;
    align-items: center;
    gap: 1.25rem;
    align-self: stretch;
    flex-wrap: wrap;
}

.articul,
.rate,
.reviews {
    display: flex;
    align-items: flex-start;
}

.articul {
    color: #414141;
}
.reviews {
    color: #414141;
    text-decoration: underline;
} 

.share,
.favourite {
    display: flex;
    align-items: center;
    gap: 0.3125rem;
    color: #606060;
}

.container {
    display: flex;
    align-items: center;
    gap: 1.25rem;
    flex: 1 0 0;
    align-self: stretch;
}

.leftIMGS {
    display: flex;
    max-width: 10%;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.9375rem;
    flex: 1 0 0;
    align-self: stretch;
    justify-content: space-between;
}

.middleIMG {
    display: flex;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
}

.descr {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.5rem;
    flex: 1 0 0;
    align-self: stretch;
}

.priceProduct {
    display: flex;
    padding: 0.625rem;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
}
.price,
.promo  {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.3125rem;
}

.price div:first-child {
    color: #606060;
}

.promo div:first-child {
    color: #414141;
}

.price div:last-child
.promo div:last-child {
    color: #BFBFBF;
}

.promo div:last-child {
    display: flex;
    align-items: center;
    gap: 0.3rem;
}

.bonusInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;
    color: #70C05B;
}
.bonusInfo img {
    height: 0.5em;
}

.notification {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.625rem;
    align-self: stretch;
}
.productions {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1.5rem;
    align-self: stretch;
}
.production {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    align-self: stretch;
}
.production .title {
    color: #414141;
}
.production .subtitle {
    color: #414141;
    font-weight: bold;
}
.rightIMGS {
    display: flex;
    max-width: 10%;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    align-self: stretch;
}
.rightIMG {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.625rem;
    flex: 1 0 0;
    align-self: stretch;
}

.rightIMGS .title {
    display: flex;
    padding-bottom: 0.625rem;
    align-items: flex-start;
    font-weight: bold;
    font-size: 1.5em;
}

.rightIMG .price {
    color: #414141;
    font-weight: bolder;
}

.middleIMG img,
.rightIMG img,
.leftIMGS img {
    width: 100%;
    height: auto;
}

@media screen and (max-width: 27em) {
    .container {
        flex-direction: column;
    }

    .leftIMGS div {
        min-width: 10em;
        max-width: 10%;
    }

    .leftIMGS {
        flex-direction: row;
        overflow: scroll;
        max-width: none;
        width: 80vw;
        align-items: center;
        justify-content: flex-start;
    }
    .containerRightIMGS::-webkit-scrollbar,
    .leftIMGS::-webkit-scrollbar {
        width: 0 !important;
        height: 0;
        display: none !important;
        background: transparent;
    }

    .rightIMGS {
        max-width: none;
        align-items: self-start;
        gap: 10px;
    }

    .containerRightIMGS {
        display: flex;
        overflow: scroll;
        width: 80vw;
    }

    .rightIMG {
        min-width: 10em;
        max-width: 10%;
    }

    .descr {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 1rem;
        flex: 1 0 0;
        align-self: stretch;
    }
    
    .productions {
        gap: 1rem;
    }
}