.reviewsContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    width: 100%;
}

.headerContainer {
    display: flex;
    height: 2rem;
    align-items: center;
    gap: 1rem;
    align-self: stretch;
}

.title {
    color: #414141;
    font-weight: bold;
}

.starsContainer {
    display: flex;
    align-items: center;
    gap: 0.25rem;
}

.textContainer {
    width: 100%;
}

.textContainer textarea{
    width: 100%;
    display: flex;
    height: 6.25rem;
    padding: 0.5rem 1rem;
    align-items: center;
    align-self: stretch;
    border-radius: 0.25rem;
    border: 1px solid #BFBFBF;
    background: #FFF;
    color: #414141;
}
