
.about {
    gap: 20px;
}

.about,
.top {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1 0 0;
    align-self: stretch;
}
.top {
    
    background: url('../../../public/assets/backgrounds/bg-about.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    gap: 20px;
}
.top .title { 
    color: #414141;
    font-size: 3em;
    font-weight: bolder;
}
.top .subtitle {
    color: #F63;
    font-size: 1.5em;
    font-weight: bolder;
}
.middle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    flex-wrap: wrap;
    gap: 20px;
}
.item {
    position: relative;
    max-width: 20em;
    min-width: 20em;
    padding: 0 20px;
}
.item .title {
    color: #000;
}

.item .subtitle {
    color: #414141;
    font-weight: bolder;
}

.icon {
    position: absolute;
    top: 0;
    left: 0px;
}
.bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3.125rem;
    flex: 1 0 0;
    align-self: stretch;
}
.logo  img {
    height: auto;
    width: 50%;
}
.text {
    display: flex;
    padding: 1.25rem 1.875rem;
    align-items: flex-start;
    border-radius: 0.625rem;
    background: #E5FFDE;
    color: #70C05B;
    font-weight: bolder;
}