.container {
    display: flex;
    align-items: center;
    align-self: stretch;
    gap: 10px;
}

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #70C05B;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #70C05B;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  } 

@media all and (max-width: 48em) {
    .switch {
        width: 50px;
        height: 24px;
    }
    .slider::before {
        height: 16px;
        width: 16px;
        left: 4px;
        bottom: 4px;
    }
}

@media all and (max-width: 27em) {
    .switch {
        width: 40px;
        height: 20px;
    }
    .slider::before {
        height: 16px;
        width: 16px;
        left: 3px;
        bottom: 2.5px;
    }
    input:checked + .slider:before {
        -webkit-transform: translateX(18px);
        -ms-transform: translateX(18px);
        transform: translateX(18px);
    }
}

@media all and (max-width: 20em) {
    .switch {
        width: 35px;
        height: 15px;
    }
    .slider::before {
        height: 12px;
        width: 12px;
        left: 3px;
        bottom: 1px;
    }
    input:checked + .slider:before {
        -webkit-transform: translateX(18px);
        -ms-transform: translateX(18px);
        transform: translateX(18px);
    }
}

