@import url('https://fonts.googleapis.com/css2?family=Inter&family=Rubik&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: transparent;
    border: none;
    outline: none;
    text-decoration: none;
    font-family: Rubik, sans-serif;
    font-size: 1vw;
    font-style: normal;
}

.productPage {
    gap: 5rem;
    display: flex;
    flex-direction: column;
}

@media all and (max-width: 48em) {
    * {
        font-size: 1.5vw;
    }
}

@media all and (max-width: 27em) {
    * {
        font-size: 2.8vw;
    }
}

@media all and (max-width: 20em) {
    * {
        font-size: 3vw;
    }
}

img {
    height: 1.5em;
}

button {
    cursor: pointer;
}




/* CONTENT */

.main {
    gap: 20px;
    display: flex;
    flex-direction: column;
}

.middleContainer {
    padding: 5.02rem 7.25rem;
    min-height: 100vh;
}

@media screen and (max-width: 48em) {
    .middleContainer {
        padding: 2.51rem 3.625rem;
    }
}

@media screen and (max-width: 27em) {
    .middleContainer {
        padding: 1.255rem 0rem;
    }
}

@media screen and (max-width: 20em) {
    .middleContainer {
        padding: 0.6275rem 0rem;
    }
}


/* RADIO BUTTONS -BUTTON */

.form_radio_btn {
    display: inline-block;
    margin-right: 10px;
    position: relative;
}

.form_radio_btn input[type=radio] {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
}

.form_radio_btn label {
    display: inline-block;
    cursor: pointer;
    padding: 0px 15px;
    line-height: 34px;
    border-radius: 6px;
    /* user-select: none; */
    color: #BFBFBF;
    text-align: center;

}

/* Checked */
.form_radio_btn input[type=radio]:checked+label {
    background: #70C05B;
    color: #FFF;
    text-align: center;
}

/* Hover */
.form_radio_btn label:hover {
    color: #666;
}

/* Disabled */
.form_radio_btn input[type=radio]:disabled+label {
    background: #efefef;
    color: #666;
}

.form_radio_btn span {
    display: none;
}

.form_radio_btn:hover input[type=radio]:disabled~span {
    position: absolute;
    z-index: 10;
    gap: 10px;
    height: auto;
    width: 100%;
    top: -50%;
    display: flex;
    align-items: center;
    padding: 0.2rem;
    border-radius: 0.5rem;
    background: #F4F6FB;
}