button {
    border-radius: 0.3125rem;
    padding: 0.625rem 1.5625rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    transition: all 0.5s linear;
}

.wg {
    color: #FFF;
    background: #70C05B;
}

.woHoverwg,
.wo {
    color: #FFF;
    background: #F63;
}

.ggoHoverWg,
.ggoHoverWo,
.ggo {
    background: #E5FFDE;
    color: #70C05B;
}

.gwHoverWo,
.gw {
    border: 1px solid #70C05B;
    color: #70C05B;
}

.ooo {
    background: #FCD5BA;
    color: #F63;
}

.bg {
    background: #F3F2F1;
    color: #606060;
}


.gwHoverWo:hover,
.ggoHoverWo:hover {
    color: #FFF;
    background: #F63;
    border: #F63 1px solid;
}

.woHoverwg:hover,
.ggoHoverWg:hover {
    color: #FFF;
    background: #70C05B;
}