.container {
    flex: 1 0 0;
}
.contacts {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    margin-bottom: 3rem;
    padding: 0px 10px;
}

.contact {
    display: flex;
    align-items: flex-start;
    gap: 0.5rem;
    flex-shrink: 0;
    flex-direction: column;
}

.contactTitle {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #414141;
}

.tel {
    color: #414141;
    text-decoration-line: underline;
    align-self: flex-end;
    font-weight: bold;
}