.vacationPanel {
    display: flex;
    flex-wrap: wrap;
    gap: 2.5rem;
}
.vacation {
    display: flex;
    padding: 2rem;
    flex-direction: column;
    align-items: flex-start;
    min-width: 20em;
    gap: 1rem;
    flex: 1 0 0;
    border-radius: 0.25rem;
    background: #f9f4e2;
    box-shadow: 1px 2px 2px 0px rgba(0, 0, 0, 0.10);
}
.title {
    color: #414141;
    font-weight: bold;
    font-size: 1.5em;
}
.requirements,
.responsibilities,
.conditions,
.call {
    color: #414141;
}

.requirements div:first-child,
.responsibilities div:first-child,
.conditions div:first-child,
.call {
    font-weight: bold;
    font-size: 1.1em;
}

.called {
    display: flex;
    align-items: center;
    gap: 5px;
}