footer {
    display: flex;
    padding: 1.75rem;
    justify-content: space-between;
    align-items: center;
    row-gap: 1.25rem;
    flex-wrap: wrap;
    background: rgb(249, 244, 226) url('../../../public/assets/backgrounds/bg-footer.png') no-repeat center;
}

a {
    color: black
}

.leftSide {
    display: flex;
    align-items: center;
    align-content: center;
    gap: 1.25rem;
    flex: 1 0 0;
    flex-wrap: wrap;
}

.leftSide .link img {
    width: 100%;
}

.link {
    display: flex;
    align-items: flex-start;
    cursor: pointer;
    align-items: center;
}

.rightSide {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    align-content: center;
    gap: 1.25rem;
    flex-wrap: wrap;
}

@media screen and (max-width: 27em) {

    footer {
        flex-direction: column;
        gap: 1.5rem;
    }
    .leftSide {
        justify-content: center;
        gap: 1.5rem;
    }

    .rightSide {
        flex-wrap: wrap;
        flex: 1 0 0;
    }
}