nav,
.links,
.user {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 0.625rem;
    align-self: stretch;
    z-index: 2;
}

.links {
    transition: all 0.5s linear;
    overflow: hidden;
    width: 11em;
}

.linksWidth {
    width: 20em;
}

.linksActive {
    width: 0;
}

.link {
    transition: all 1s linear;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
    flex: 1 0 0;
    cursor: pointer;
}


.link button {
    flex: 1 0 0;
    padding: 5px 10px;
    background: #70C05B;
    color: #fff;
    text-align: center;
    border-radius: 10px;
}

.link {
    padding: 0.625rem 0;
}

.linkIMG {
    position: relative;
}

.countLink {
    padding: 8px;
    font-size: 0.8em;
    position: absolute;
    right: -10px;
    top: 6px;
    background-color: #ff6633;
    color: #fff;
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
}


.linkIMG,
.linkTitle {
    display: flex;
    align-items: flex-start;
    gap: 0.625rem;
}

.userIMG,
.userName,
.userArrow {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: stretch;
}

.userIMG img {
    width: 2rem;
    height: 2rem;
}

.user {
    justify-content: center;
}

.userArrow {
    cursor: pointer;
    transition: all 0.5s linear;
}

.linksActive~.user .userArrow {
    transform: scale(-1, -1);
}

.userIMGBottom {
    display: none;
}

@media screen and (max-width: 27em) {
    .userArrow {
        display: none;
    }

    .links {
        transition: all 0.5s linear;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        width: fit-content;
        background: white;
        border-radius: 10px;
        position: absolute;
        gap: 1rem;
        padding: 10px;
        bottom: 0;
        right: -5px;
    }

    .link {
        margin-bottom: -120%;
        opacity: 0;
        transition: all 0.5s linear;
    }

    .linksActive .link {
        margin-bottom: 0;
        opacity: 1;
    }

    .linksActive .link:first-child {
        margin-top: 5px;
    }

    .linksActive .link:last-child {
        margin-bottom: 80%;
    }

    .userIMG {
        display: none;
    }

    .userIMGBottom {
        display: flex;
    }

    .user {
        background-color: #fff;
        padding: 0 10px;
    }

    .userName {
        display: none;
    }

    .link button {
        display: none;
    }
}